import React, { useEffect, useState } from 'react';
import LayoutPage from '../Layout/layout';
import { useHistory, useParams } from 'react-router-dom';
import firebase from '../firebase';
import { NavLink } from 'react-router-dom';
const Question = () => {
    const [todoList, setTodoList] = useState([]);
    const [listQuestion, setListQuestion] = useState([]);
    const [ch, setCh] = useState('');
    const [aAs, setAAs] = useState('');
    const [bAs, setBAs] = useState('');
    const [cAs, setCAs] = useState('');
    const [dAs, setDAs] = useState('');
    const [tAs, setTAs] = useState('');
    const [ed, setEd]  = useState(false);

    const params = useParams();
    const history = useHistory();
    useEffect(() => {
        const getDataById =  (id) =>{
            const todoRef = firebase.database().ref('test').child(id);
            //   console.log(todoRef);
            todoRef.on('value', (snapshot) => {
                const todos = snapshot.val();
                setTodoList(todos);       
              });
            
        }
        const getListQuestion = (id) => {
          const todoRef = firebase.database().ref('question').orderByChild('iddethi').equalTo(id);
        todoRef.on('value', (snapshot) => {
          const todos = snapshot.val();
          const data = [];
          for (let id in todos) {
            data.push({ id, ...todos[id] });
          }
          setListQuestion(data);
          
        });
        }
        getDataById(params.id);
        getListQuestion(params.id);

    },[])
 
    const onFinish = async () => {
        // const question = firebase.database().ref('question');
        const d = new Date();
            let add = {
                iddethi: params.id,
                idcauhoi: d.getTime(),
                cauhoi: ch,
                as1: aAs,
                as2: bAs,
                as3: cAs,
                as4: dAs,
                true: tAs
            }
            if(ed){
              const update = await firebase.database().ref('question/'+params.id).update(add);
              return window.location.href = '/question/'+params.id;
            }else{
              const question = await firebase.database().ref('question').push(add);
              setAAs('');
              setBAs('');
              setCAs('');
              setDAs('');
              setTAs('');
              setCh('');
              return history.push('/question/'+params.id);
            }
        
            
      };
      const edit = (key) => {
        console.log(key);
        setCh(key.cauhoi);
        setAAs(key.as1);
        setBAs(key.as2);
        setCAs(key.as3);
        setDAs(key.as4);
        setTAs(key.true);
        setEd(true);
      }
      const DeleteQuetion = (id) => {
        const addSt = firebase.database().ref('question/'+id)
        addSt.remove()
      }
    return(
        <LayoutPage>
            <h3>Câu hỏi bài thi: {todoList.baithi}</h3>
            <h5>Hình thức thi: {todoList.loai === 'TN' ? 'Trắc nghiệm': 'Tự luận'}</h5>
            <div className="text-right">
              <button className="btn btn-primary" type='button' data-toggle="modal" data-target="#myModal">
                Thêm câu hỏi
              </button>
            </div>
            <table className="table table-striped">
  <thead>
    <tr>
      <th>Câu hỏi</th>
      <th>Đáp án A</th>
      <th>Đáp án B</th>
      <th>Đáp án C</th>
      <th>Đáp án D</th>
      <th>#</th>
    </tr>
  </thead>
  <tbody>
      {listQuestion ? listQuestion.map((key,index) => (
          <tr>
          <td>{key.cauhoi}</td>
          <td>{key.as1}</td>
          <td>{key.as2}</td>
          <td>{key.as3}</td>
          <td>{key.as4}</td>
          <td>
              <NavLink to={'#'}><button className="btn btn-secondary" data-toggle="modal" data-target="#myModal" onClick={() =>edit(key) }>Sửa</button></NavLink>
              <NavLink to={'#'}><button className="btn btn-danger" onClick={() => DeleteQuetion(key.id)}>Xóa</button></NavLink>
          </td>
        </tr>
      )) : null}
    
  </tbody>
</table>

<div id="myModal" className="modal fade" role="dialog">
  <div className="modal-dialog">
    {/* Modal content*/}
    <div className="modal-content">
      <div className="modal-header">
        <button type="button" className="close" data-dismiss="modal">×</button>
        <h4 className="modal-title">câu hỏi</h4>
      </div>
      <div className="modal-body">
      <div className="form-group">
        <label htmlFor="usr">Câu hỏi:</label>
        <input type="text" className="form-control" onChange={(e)=>setCh(e.target.value)} defaultValue={ch} />
      </div>
      <div className="form-group">
        <label htmlFor="usr">Đáp án A:</label>
        <input type="text" className="form-control" defaultValue={aAs} onChange={(e) => setAAs(e.target.value)}/>
      </div>
      <div className="form-group">
        <label htmlFor="usr">Đáp án B:</label>
        <input type="text" className="form-control" defaultValue={bAs} onChange={(e) => setBAs(e.target.value)}/>
      </div>
      <div className="form-group">
        <label htmlFor="usr">Đáp án C:</label>
        <input type="text" className="form-control" defaultValue={cAs} onChange={(e) => setCAs(e.target.value)}/>
      </div>
      <div className="form-group">
        <label htmlFor="usr">Đáp án D:</label>
        <input type="text" className="form-control" defaultValue={dAs}  onChange={(e) => setDAs(e.target.value)}/>
      </div>
      <div className="form-group">
        <label htmlFor="usr">Đáp án đúng:</label>
        <input type="text" className="form-control" defaultValue={tAs} onChange={(e) => setTAs(e.target.value)}/>
      </div>
      </div>
      <div className="modal-footer">
        <button type="button" className="btn btn-default" data-dismiss="modal">Close</button>
        <button type="button" className="btn btn-primary" onClick={onFinish}>Lưu lại</button>
      </div>
    </div>
  </div>
</div>

        </LayoutPage>
    )
}

export default Question;